import React from "react";
import { useNavigate } from 'react-router-dom';
import api from '../api';
import {
  LineChart, Line, XAxis, YAxis, Tooltip, CartesianGrid, ResponsiveContainer,
  BarChart, Bar, Legend
} from "recharts";
import "./Dashboard.css";

const salesData = [
  { day: "Mon", sales: 4200 },
  { day: "Tue", sales: 3500 },
  { day: "Wed", sales: 5800 },
  { day: "Thu", sales: 4800 },
  { day: "Fri", sales: 7600 },
  { day: "Sat", sales: 9000 },
  { day: "Sun", sales: 6300 },
];

const weeklyData = [
  { name: "Week 1", revenue: 32000 },
  { name: "Week 2", revenue: 41000 },
  { name: "Week 3", revenue: 37500 },
  { name: "Week 4", revenue: 46000 },
];

const recentProducts = [
  { id: 101, name: "Neo Monitor X1", stock: 8, price: 12999 },
  { id: 102, name: "Ultra Keyboard", stock: 21, price: 1999 },
  { id: 103, name: "Flux Router", stock: 4, price: 6999 },
  { id: 104, name: "PixelPhone Z", stock: 12, price: 54999 },
];

const Dashboard = () => {
  // Decide whether user is admin-like (staff or has can_view_reports) or a normal user
  const profileRaw = localStorage.getItem('profile');
  let isAdminLike = false;
  try {
    const p = profileRaw ? JSON.parse(profileRaw) : null;
    isAdminLike = (p && p.can_view_reports) || (p && p.is_staff);
  } catch (e) { isAdminLike = false; }

  if (!isAdminLike) {
    // Simple user dashboard / homepage substitute
    return (
      <div style={{padding:20}}>
        <h2>Welcome to your dashboard</h2>
        <p>Quick actions to help you generate invoices and track your work.</p>

        <div style={{display:'flex', gap:12, marginTop:16}}>
          <a href="/generate-invoice" className="btn-primary">Generate Invoice</a>
          <a href="/home" className="btn-ghost">Home</a>
        </div>

        <div style={{marginTop:24}}>
          <h3>Your recent invoices</h3>
          <p>There are no recent invoices in this demo. Use Generate Invoice to create a new one.</p>
        </div>
      </div>
    );
  }

  // Admin analytics (original dashboard view)
  return (
    <div className="dash-root">
      <div className="dash-header">
        <div>
          <h1 className="dash-title">TradeTrack  Analytics</h1>
          <p className="dash-sub">Live overview  Dark neon theme</p>
        </div>
        <div className="dash-cta">
          <button className="btn-ghost" onClick={async () => {
            // Fetch report and generate CSV from sales_by_product
            try {
              const res = await api.apiFetch('/api/reports/sales/');
              if (!res.ok) { alert('Failed to fetch reports: ' + (res.data || res.status)); return; }
              const report = res.data;
              const rows = [['product_id','product_name','total_quantity','total_sales']];
              (report.sales_by_product || []).forEach(p => rows.push([p.product_id, p.product_name, p.total_quantity, p.total_sales]));
              const csv = rows.map(r => r.map(v => '"' + String(v).replace(/"/g,'""') + '"').join(',')).join('\n');
              const blob = new Blob([csv], { type: 'text/csv' });
              const url = URL.createObjectURL(blob);
              const a = document.createElement('a'); a.href = url; a.download = 'sales_by_product.csv'; document.body.appendChild(a); a.click(); a.remove(); URL.revokeObjectURL(url);
            } catch (e) { alert('Export failed: ' + e.message); }
          }}>Export CSV</button>
          <button className="btn-primary" onClick={() => {
            // navigate to generate invoice
            const nav = window.location; nav.href = '/generate-invoice';
          }}>New Invoice</button>
        </div>
      </div>

      {/* Cards */}
      <div className="cards-grid">
        <div className="card neon">
          <div className="card-top">
            <span className="card-label">Total Products</span>
            <span className="card-value">1,240</span>
          </div>
          <div className="card-bottom">Stock tracked  realtime</div>
        </div>

        <div className="card neon">
          <div className="card-top">
            <span className="card-label">Total Sales (M)</span>
            <span className="card-value"> 3.8M</span>
          </div>
          <div className="card-bottom">Last 30 days</div>
        </div>

        <div className="card neon">
          <div className="card-top">
            <span className="card-label">Today Revenue</span>
            <span className="card-value"> 22,400</span>
          </div>
          <div className="card-bottom">Updated 10m ago</div>
        </div>

        <div className="card neon alert">
          <div className="card-top">
            <span className="card-label">Low Stock</span>
            <span className="card-value">5 items</span>
          </div>
          <div className="card-bottom">Needs restock</div>
        </div>
      </div>

      {/* Charts area */}
      <div className="charts-row">
        <div className="chart-card">
          <h3 className="chart-title">Daily Sales (last 7 days)</h3>
          <ResponsiveContainer width="100%" height={240}>
            <LineChart data={salesData} margin={{ top: 10, right: 10, left: -10, bottom: 0 }}>
              <CartesianGrid stroke="rgba(255,255,255,0.04)" />
              <XAxis dataKey="day" stroke="rgba(255,255,255,0.6)" />
              <YAxis stroke="rgba(255,255,255,0.6)" />
              <Tooltip contentStyle={{ background: "#0b1220", borderRadius: 8 }} />
              <Line type="monotone" dataKey="sales" stroke="#4ee0ff" strokeWidth={3} dot={{ r: 4 }} />
            </LineChart>
          </ResponsiveContainer>
        </div>

        <div className="chart-card">
          <h3 className="chart-title">Weekly Revenue</h3>
          <ResponsiveContainer width="100%" height={240}>
            <BarChart data={weeklyData} margin={{ top: 10, right: 0, left: -10, bottom: 0 }}>
              <CartesianGrid stroke="rgba(255,255,255,0.03)" />
              <XAxis dataKey="name" stroke="rgba(255,255,255,0.6)" />
              <YAxis stroke="rgba(255,255,255,0.6)" />
              <Tooltip contentStyle={{ background: "#0b1220", borderRadius: 8 }} />
              <Bar dataKey="revenue" fill="#8a6bff" radius={[6,6,0,0]} />
              <Legend wrapperStyle={{ color: "rgba(255,255,255,0.7)" }} />
            </BarChart>
          </ResponsiveContainer>
        </div>
      </div>

      {/* Recent Products */}
      <div className="table-card">
        <div className="table-header">
          <h3>Recent Products</h3>
          <div className="table-actions">
            <input className="search-input" placeholder="Search product..." />
            <button className="btn-small">View all</button>
          </div>
        </div>

        <table className="neon-table">
          <thead>
            <tr>
              <th>ID</th>
              <th>Product</th>
              <th>Stock</th>
              <th>Price ()</th>
              {/* Only show action column to admin-like users */}
              {(() => {
                const profileRaw = localStorage.getItem('profile');
                let showAction = false;
                try {
                  const p = profileRaw ? JSON.parse(profileRaw) : null;
                  showAction = (p && p.can_view_reports) || (p && p.is_staff);
                } catch (e) { showAction = false; }
                return showAction ? <th>Action</th> : null;
              })()}
            </tr>
          </thead>
          <tbody>
            {recentProducts.map((p) => (
              <tr key={p.id}>
                <td>{p.id}</td>
                <td>{p.name}</td>
                <td className={p.stock <= 5 ? "low-stock" : ""}>{p.stock}</td>
                <td>{p.price.toLocaleString()}</td>
                {(() => {
                  const profileRaw = localStorage.getItem('profile');
                  let showAction = false;
                  try {
                    const p = profileRaw ? JSON.parse(profileRaw) : null;
                    showAction = (p && p.can_view_reports) || (p && p.is_staff);
                  } catch (e) { showAction = false; }
                  if (!showAction) return null;
                  return (
                    <td>
                      <button className="link-btn" onClick={() => alert("Edit coming soon")}>Edit</button>
                      <button className="danger-btn" onClick={() => alert("Delete coming soon")}>Delete</button>
                    </td>
                  );
                })()}
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default Dashboard;
