import React, { useEffect, useState } from 'react';
import api from '../api';

export default function Reports() {
  const [report, setReport] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');

  useEffect(() => {
    let mounted = true;
    async function load() {
      setLoading(true);
      let path = '/api/reports/sales/';
      const params = [];
      if (startDate) params.push(`start_date=${encodeURIComponent(startDate)}`);
      if (endDate) params.push(`end_date=${encodeURIComponent(endDate)}`);
      if (params.length) path += '?' + params.join('&');
      const res = await api.apiFetch(path);
      if (!mounted) return;
      if (res.ok) { setReport(res.data); setError(null); }
      else {
        let msg = res.data || `Status ${res.status}`;
        if (res.status === 403) msg = 'Permission denied: ensure your account has report access.';
        setError(msg);
      }
      setLoading(false);
    }
    load();
    return () => { mounted = false; };
  }, []);

  if (loading) return <div>Loading reports...</div>;
  if (error) return (
    <div style={{color:'red',padding:20}}>
      <div><strong>Error:</strong> {String(error)}</div>
      <div style={{marginTop:8}}>
        <button onClick={() => { setError(null); setLoading(true); setReport(null); window.location.reload(); }}>Retry</button>
      </div>
    </div>
  );
  if (!report) return <div>No data</div>;

  return (
    <div style={{padding:20}}>
      <h2>Sales Report</h2>
      <div style={{display:'flex', gap:20}}>
        <div>
          <strong>Total Sales:</strong> {report.total_sales.toLocaleString()}
        </div>
        <div>
          <strong>Invoice Count:</strong> {report.invoice_count}
        </div>
      </div>

      <h3 style={{marginTop:20}}>Top Products</h3>
      <table className="table">
        <thead>
          <tr><th>Product</th><th>Quantity</th><th>Sales</th></tr>
        </thead>
        <tbody>
          {report.top_products && report.top_products.map(p => (
            <tr key={p.product_id}>
              <td>{p.product_name}</td>
              <td>{p.total_quantity}</td>
              <td>{p.total_sales.toLocaleString()}</td>
            </tr>
          ))}
        </tbody>
      </table>

      <h3 style={{marginTop:20}}>Monthly (last 12)</h3>
      <table className="table">
        <thead><tr><th>Year</th><th>Month</th><th>Sales</th></tr></thead>
        <tbody>
          {report.monthly_sales_last_12.map(m => (
            <tr key={`${m.year}-${m.month}`}>
              <td>{m.year}</td>
              <td>{m.month}</td>
              <td>{m.sales.toLocaleString()}</td>
            </tr>
          ))}
        </tbody>
      </table>

      <div style={{marginTop:20}}>
        <label style={{marginRight:8}}>Start date: <input type="date" value={startDate} onChange={e=>setStartDate(e.target.value)} /></label>
        <label style={{marginLeft:8}}>End date: <input type="date" value={endDate} onChange={e=>setEndDate(e.target.value)} /></label>
        <button style={{marginLeft:12}} onClick={() => { setLoading(true); setReport(null); setError(null); (async()=>{ const res = await api.apiFetch(`/api/reports/sales/?start_date=${encodeURIComponent(startDate)}&end_date=${encodeURIComponent(endDate)}`); if (res.ok) setReport(res.data); else setError(res.data||res.status); setLoading(false); })(); }}>Apply</button>

        <button style={{marginLeft:12}} onClick={() => {
          const blob = new Blob([JSON.stringify(report, null, 2)], {type:'application/json'});
          const url = URL.createObjectURL(blob);
          const a = document.createElement('a');
          a.href = url; a.download = 'sales_report.json'; document.body.appendChild(a); a.click(); a.remove();
          URL.revokeObjectURL(url);
        }}>Download JSON</button>

        <button style={{marginLeft:12}} onClick={() => {
          // export sales_by_product as CSV
          const rows = [['product_id','product_name','total_quantity','total_sales']];
          (report.sales_by_product || []).forEach(p => rows.push([p.product_id, p.product_name, p.total_quantity, p.total_sales]));
          const csv = rows.map(r => r.map(v => '"' + String(v).replace(/"/g,'""') + '"').join(',')).join('\n');
          const blob = new Blob([csv], { type: 'text/csv' });
          const url = URL.createObjectURL(blob);
          const a = document.createElement('a'); a.href = url; a.download = 'sales_by_product.csv'; document.body.appendChild(a); a.click(); a.remove(); URL.revokeObjectURL(url);
        }}>Export CSV</button>
      </div>
    </div>
  );
}
