import React, { useState, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import api, { setToken } from "../api";
import "./Login.css";

const Login = () => {
  const navigate = useNavigate();
  const location = useLocation();

  // Run only once when page loads
  useEffect(() => {
    if (localStorage.getItem("token")) {
      navigate("/dashboard", { replace: true });
    }
  }, [navigate]);

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(null);

  const handleLogin = (e) => {
    e.preventDefault();

    // Call backend token endpoint
    (async () => {
      setError(null);
      setSuccess(null);
      const r = await api.obtainToken(email, password);
      console.log('obtainToken response', r);
      if (!r.ok) {
        const msg = r.data || `HTTP ${r.status}`;
        setError(typeof msg === 'string' ? msg : JSON.stringify(msg));
        return;
      }
      const token = r.data.token;
      // store token
      setToken(token);
      console.log('token stored:', token, 'localStorage.token=', localStorage.getItem('token'));
      // notify other listeners in this tab that auth changed
      window.dispatchEvent(new Event("authChange"));

      // Fetch profile and store it for quick UI checks
      const me = await api.fetchMe();
      console.log('fetchMe response', me);
      if (!me.ok) {
        const detail = me.data || `HTTP ${me.status}`;
        setError(typeof detail === 'string' ? detail : JSON.stringify(detail));
        return;
      }
      if (me && me.data) {
        localStorage.setItem('profile', JSON.stringify(me.data));
        setSuccess(`Logged in as ${me.data.username}`);
      } else {
        setSuccess('Logged in');
      }

      // If the user was redirected here from a protected route, return them there
      // otherwise send admin users to /dashboard and normal users to /home
      const fromPath = location.state?.from?.pathname;
      let target = fromPath;
      try {
        const meRaw = localStorage.getItem('profile');
        const me = meRaw ? JSON.parse(meRaw) : null;
        const isAdminLike = me && (me.is_staff || me.can_view_reports);
        if (!target) target = isAdminLike ? '/dashboard' : '/home';
      } catch (e) {
        if (!target) target = '/dashboard';
      }
      // Force a full page navigation so the app reloads and the navbar/profile state is consistent
      // Use location.replace to avoid creating a back entry
      window.location.replace(target);
    })();
  };

  // demo autofill helper removed

  return (
    <div className="login-wrapper">
      <form className="login-box" onSubmit={handleLogin}>
        <h2 className="login-title">TradeTrack Login</h2>

        {/* demo autofill removed */}

        <input
          type="email"
          placeholder="Email address"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          required
        />

        <input
          type="password"
          placeholder="Password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          required
        />

        <button type="submit" className="login-btn">Login</button>

        {error && <p className="login-error">{error}</p>}
        {success && <p className="login-success">{success}</p>}

        <p className="login-hint">Demo: admin@tradetrack.com / admin123</p>
        <p className="forgot-link"><a href="/forgot-password">Forgot Password?</a></p>
        <p className="signup-link">Dont have an account? <a href="/signup">Sign Up</a></p>
      </form>
    </div>
  );
};

export default Login;
