import React, { useEffect, useState } from "react";
import { BrowserRouter as Router, Routes, Route, Link, useNavigate } from "react-router-dom";

import UserHome from "./pages/UserHome";
import Login from "./pages/Login";
import Signup from "./pages/Signup";
import ForgotPassword from "./pages/ForgotPassword";
import Products from "./pages/Products";
import AddProduct from "./pages/AddProduct";
import GenerateInvoice from "./pages/GenerateInvoice";
import InvoicePreview from "./pages/InvoicePreview";
import Dashboard from "./pages/Dashboard";
import Reports from "./pages/Reports";

import ProtectedRoute from "./components/ProtectedRoute";
import "./components/Navbar.css";  // Premium NavBar Styles

function App() {
  const [isAuth, setIsAuth] = useState(!!localStorage.getItem("token"));

  // Keep auth state in sync when other parts of the app change localStorage.
  useEffect(() => {
    const onAuthChange = () => setIsAuth(!!localStorage.getItem("token"));
    // Custom event for same-tab updates
    window.addEventListener("authChange", onAuthChange);
    // storage event for other tabs
    window.addEventListener("storage", onAuthChange);
    return () => {
      window.removeEventListener("authChange", onAuthChange);
      window.removeEventListener("storage", onAuthChange);
    };
  }, []);

  // Optionally clear any stored token on app start to force manual login
  useEffect(() => {
    // Only clear stored token/profile if REACT_APP_FORCE_MANUAL_LOGIN=1 is set.
    // This avoids removing a freshly-stored token right after login (which caused an immediate redirect).
    const forceManual = process.env.REACT_APP_FORCE_MANUAL_LOGIN === '1';
    if (forceManual) {
      if (localStorage.getItem('token')) {
        localStorage.removeItem('token');
        localStorage.removeItem('profile');
        window.dispatchEvent(new Event('authChange'));
      }
    }
  }, []);

  return (
    <Router>

      {/* ================= NAVBAR ================= */}
      <header className="navbar">
        <div className="nav-left">
          <span className="logo">TradeTrack</span>
        </div>

        <nav className="nav-links">
          {/* Home: show to unauthenticated users and also to authenticated normal users */}
          {(!isAuth) ? <Link to="/">Home</Link> : null}

          {/* Show admin links when user can view reports or is staff */}
          {(() => {
            const profileRaw = localStorage.getItem('profile');
            let p = null;
            try { p = profileRaw ? JSON.parse(profileRaw) : null; } catch (e) { p = null; }
            const isAdminLike = (p && p.can_view_reports) || (p && p.is_staff);
            if (isAdminLike) {
              return (
                <>
                  <Link to="/dashboard">Dashboard</Link>
                  <Link to="/reports">Reports</Link>
                  <Link to="/products">Products</Link>
                  <Link to="/add-product">Add Product</Link>
                </>
              );
            }
            // Authenticated normal users see Home + Generate Invoice
            if (isAuth) {
              return (
                <>
                  <Link to="/home">Home</Link>
                  <Link to="/generate-invoice">Generate Invoice</Link>
                </>
              );
            }
            return null;
          })()}

          {/* login/logout logic */}
          {isAuth ? (
            <button
              className="logout-btn"
              onClick={() => {
                  localStorage.removeItem("token");
                  localStorage.removeItem('profile');
                  // notify other listeners in this tab
                  window.dispatchEvent(new Event("authChange"));
                  // navigate to login (replace full reload with SPA navigation)
                  window.location.href = "/login";
                }}
            >
              Logout
            </button>
          ) : (
            <Link className="login-btn" to="/login">
              Login
            </Link>
          )}
        </nav>
      </header>

      {/* ================= ROUTES ================= */}
      <main>
        <Routes>

          {/* Public */}
          <Route path="/" element={<Login />} />
          <Route path="/home" element={<UserHome />} />
          <Route path="/login" element={<Login />} />
          <Route path="/signup" element={<Signup />} />
          <Route path="/forgot-password" element={<ForgotPassword />} />

          {/* Protected Routes */}
          <Route
            path="/dashboard"
            element={
              <ProtectedRoute>
                <Dashboard />
              </ProtectedRoute>
            }
          />

          <Route
            path="/reports"
            element={
              <ProtectedRoute>
                <Reports />
              </ProtectedRoute>
            }
          />

          <Route
            path="/products"
            element={
              <ProtectedRoute>
                <Products />
              </ProtectedRoute>
            }
          />

          <Route
            path="/add-product"
            element={
              <ProtectedRoute>
                <AddProduct />
              </ProtectedRoute>
            }
          />

          <Route
            path="/generate-invoice"
            element={
              <ProtectedRoute>
                <GenerateInvoice />
              </ProtectedRoute>
            }
          />

          <Route path="/invoices/:id/preview" element={<InvoicePreview />} />

        </Routes>
      </main>

    </Router>
  );
}

export default App;
