import React, { useRef, useEffect } from 'react';
import './UserHome.css';

const UserHome = () => {
  const containerRef = useRef(null);

  // Simple mousemove parallax: set CSS vars for blobs to follow the cursor slightly
  useEffect(() => {
    const el = containerRef.current;
    if (!el) return;
    const onMove = (e) => {
      const rect = el.getBoundingClientRect();
      const x = (e.clientX - rect.left) / rect.width;
      const y = (e.clientY - rect.top) / rect.height;
      el.style.setProperty('--mx', (x - 0.5).toFixed(3));
      el.style.setProperty('--my', (y - 0.5).toFixed(3));
    };
    window.addEventListener('mousemove', onMove);
    return () => window.removeEventListener('mousemove', onMove);
  }, []);

  // Entrance trigger for subtle staggered animations
  useEffect(() => {
    const el = containerRef.current;
    if (!el) return;
    const t = setTimeout(() => el.classList.add('entered'), 80);
    return () => clearTimeout(t);
  }, []);

  return (
    <section className="userhome-hero" ref={containerRef}>
      <div className="animated-bg">
        <div className="blob b1" aria-hidden></div>
        <div className="blob b2" aria-hidden></div>
        <div className="blob b3" aria-hidden></div>
      </div>

      <div className="userhome-content">
        <h1>Welcome back, <span>Trader</span></h1>
        <p className="lead">Quickly create invoices and keep your inventory in sync. Use the navigation to access features.</p>

        <div className="feature-grid">
          <div className="feature">
            <h4>Fast Invoicing</h4>
            <p>Create invoices in seconds from the Generate Invoice tab.</p>
          </div>
          <div className="feature">
            <h4>Stock Alerts</h4>
            <p>Get notified for low-stock items so you never run out.</p>
          </div>
          <div className="feature">
            <h4>Reports</h4>
            <p>Admins can view sales reports and analytics in Dashboard.</p>
          </div>
        </div>

        <div className="hero-illustration" aria-hidden>
          <svg width="380" height="220" viewBox="0 0 380 220" xmlns="http://www.w3.org/2000/svg">
            <g fill="none" fillRule="evenodd">
              <rect width="380" height="220" rx="12" fill="rgba(255,255,255,0.03)" />
              <circle cx="80" cy="70" r="26" fill="#7c3aed" opacity="0.9" />
              <rect x="140" y="40" width="160" height="12" rx="6" fill="#60a5fa" />
              <rect x="140" y="66" width="120" height="10" rx="5" fill="#93c5fd" />
              <rect x="140" y="88" width="80" height="10" rx="5" fill="#c7d2fe" />
            </g>
          </svg>
        </div>
      </div>
    </section>
  );
};

export default UserHome;
